import React from 'react';
import { itemActionCreator } from '../actions/itemactioncreator';
import { AddItems } from '../components/searchpage/AddItems';
import { NoRecordFound } from '../components/searchpage/NotRecordFound';
import { SearchInput } from '../components/searchpage/SearchInput';
import  SearchList  from '../components/searchpage/SearchList';
import { TotalItemsInCart } from '../components/searchpage/TotalItems';
import { fetchProducts } from '../utils/ajax';
import { itemAsyncActionCreator } from '../utils/asyncmiddleware';
import { CONFIG } from '../utils/constants';
import {store} from '../utils/store';
import { Loading } from '../widgets/Loading';
export class SearchPage
//extends React.Component{
extends React.PureComponent{
    constructor(props){
        super(props);
        this.toggle = true;
        this.state = {products:[]};
        this.isLoading = true;
        console.log('1. Constructor Invoke');
    }
    static getDerivedStateFromProps(nextProps, prevState) { // Rarely Used

    }


    UNSAFE_componentWillMount(){
        console.log("2. Will Mount Call");
    }
    render(){

        console.log("3. Render");
        let jsx = (<></>);
        if(this.isLoading){
            return  (<Loading/>);
        }
        else{
        if(this.toggle){
            jsx = (<AddItems/>);
        }
        return (
            <>
            <button onClick={()=>{
                this.toggle = !this.toggle;
                console.log('Toggle ',this.toggle);
                this.setState({...this.state, 'toggle':this.toggle});
            }}>Toggle It</button>
            {jsx}
            <TotalItemsInCart  price='54543'/>
            <SearchInput/>
            <SearchList/>
            {/* {this.state.products.length===0?<NoRecordFound/>
            :<SearchList products={this.state.products}/>} */}

            </>
        );
        }
    }
    dataCallBack(responseData){
        console.log('Response Data in CallBack ',responseData);
        let object = JSON.parse(responseData);
        console.log(typeof responseData);
        console.log("Object is ",object.mobiles);
       // const dispatchData = itemActionCreator(object.mobiles,CONFIG.ACTION_TYPES.LOAD);
       const dispatchData = itemAsyncActionCreator(object.mobiles,CONFIG.ACTION_TYPES.LOAD);
        console.log('DIS DATA ',dispatchData);
        console.log('DIS DATA ',dispatchData);
        store.dispatch(dispatchData);
        //this.setState({products:object.mobiles});
    }
    componentDidMount(){
        //let bindedWithThis = this.dataCallBack.bind(this);
        //fetchProducts(bindedWithThis);
        //fetchProducts(this.dataCallBack.bind(this));
        console.log('Component Did Mount Call');
        setTimeout(()=>{
            const promise = fetchProducts();
        promise.then(response=>{
            this.isLoading = false;
            this.setState({...this.state, isLoading:this.isLoading});
            console.log(response.data.mobiles);
            const dispatchData = itemAsyncActionCreator(response.data.mobiles,CONFIG.ACTION_TYPES.LOAD);
            store.dispatch(dispatchData);
        }).catch(err=>{
            console.log(err);
        })
        },5000);

        console.log("4. Did Mount Call");
    }
    componentDidUpdate(){
        console.log('Change Happen');
    }
    componentWillUnmount(){
        // Clean Up code
    }
    /*shouldComponentUpdate(nextProps, nextStates){
        //return true;

         if(this.props.x != nextProps.x){
         return true;
         }
         return false;
        // this.state == nextStates
        // return false;
    }*/
}